.topbar {
 height: 51px;
 background-color: var(--slate-grey);
 font-family: 'lato', Arial, Helvetica, sans-serif;
 font-weight: 700;
}

.topbar__container {
 max-width: 1176px;
 height: inherit;
 margin: 0 auto;
}

nav {
 height: inherit;
}

nav ul {
 height: inherit;
 display: -webkit-box;
 display: -ms-flexbox;
 display: flex;
 -webkit-column-gap: 10px;
 column-gap: 10px;
 -webkit-box-align: center;
 -ms-flex-align: center;
 align-items: center;
 -webkit-box-pack: justify;
 -ms-flex-pack: justify;
 justify-content: space-between;
 margin: 0;
}

nav ul li {
 list-style: none;
 height: inherit;
 line-height: 50px;
 font-size: 1.3125rem;
 text-transform: capitalize;
}

nav ul li a {
 text-decoration: none;
 color: var(--white);
}

ul li:nth-child(2) {
 margin-right: auto;
}

ul li:nth-child(3) {
 border-right: 1px solid rgba(0, 0, 0, 0.4);
 border-left: 1px solid rgba(0, 0, 0, 0.4);
 padding: 0 15px;
 font-size: 1rem;
 text-transform: uppercase;
}

ul li:nth-child(4) {
 font-family: 'gess', Arial, Helvetica, sans-serif;
 font-weight: normal;
 font-size: 1rem;
}

ul li span {
 vertical-align: middle;
}
