.content {
 max-width: 1176px;
 background-color: #fff;
 margin: 0 auto;
 padding: 4rem 0;
 border-right: 20px solid var(--cool-grey);
 border-left: 20px solid var(--cool-grey);
 height: 100%;
}

.tableWrap {
 width: 100%;
 background-color: var(--slate-grey-two);
 padding: 2rem 1.5rem;
 margin: 2rem auto;
 font-family: 'lato', Arial, Helvetica, sans-serif;
}
.tableWrap h3 {
 font-weight: bold;
 color: var(--style-primary);
 margin-bottom: 1rem;
}
.tableWrap i {
 font-style: normal;
 color: #fff;
 margin-bottom: 1rem;
 display: block;
}
ul.headingIcons {
 display: flex;
 justify-content: center;
 align-items: center;

 max-width: 150px;

 flex-wrap: wrap;
 column-gap: 50px;
 row-gap: 10px;
 margin-left: auto;
 padding-bottom: 10px;
}
.tableWrap ul.headingIcons li {
 list-style: none;
 padding: 0;
 margin: 0;
 color: #fff;
 border: none;
 text-align: center;
 width: 50px;
}

ul.headingIcons li span {
 display: block;
 margin: 0 auto;
}
